html {
  font-size: 62.5%;
  box-sizing: border-box;
  height: 100%;
}

body {
  min-height: 100%;
  display: flex;
  flex-direction: column;
  background-color: #05060e;
  color: #FFFFFF;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Poppins", sans-serif;
  font-size: 1.6rem;
}

.contenedor {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
}

.texto-centrado {
  text-align: center;
}

main {
  background: rgb(2, 12, 54);
  background: linear-gradient(0deg, rgb(2, 12, 54) 0%, rgb(5, 6, 14) 100%);
}

h1, h2, h3 {
  font-family: "Poppins", sans-serif;
  margin: 0 0 2.5rem 0;
  font-weight: 900;
  color: #FFFFFF;
  text-align: center;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3rem;
  text-align: center;
}

h4 {
  font-weight: 700;
}

p {
  color: #FFFFFF;
  font-size: 2rem;
}

.navegacion {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
}
.navegacion nav {
  display: flex;
  flex-direction: column;
}
.navegacion a {
  margin: 2rem auto;
  color: transparent;
  font-weight: 700;
  font-size: 2.5rem;
  position: relative;
  -webkit-text-stroke: 1px rgb(255, 255, 255);
}
@media (min-width: 768px) {
  .navegacion a {
    margin: 2rem 3rem 0 0;
  }
}
.navegacion a::before {
  content: attr(data-text);
  position: absolute;
  overflow: hidden;
  color: var(--clr);
  width: 0;
  transition: 1s;
  -webkit-text-stroke: 1px #CE9178;
}
.navegacion a:hover::before {
  width: 110%;
  filter: drop-shadow(0px 0px 10px var(--clr));
}
.navegacion img {
  margin-top: 2rem;
  width: 6rem;
  height: 6rem;
}

.bg-nav {
  background-color: #252532;
}

@media (min-width: 768px) {
  .header .bg-nav {
    position: fixed;
    width: 100%;
    z-index: 100;
  }
  .header .navegacion {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    padding: 1rem 0 0.5rem;
  }
  .header .navegacion a {
    margin-top: 0;
  }
  .header .navegacion nav {
    display: flex;
    flex-direction: row;
    align-items: center;
  }
  .header .navegacion img {
    margin-top: 0;
  }
}
.contenido {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin-top: 5rem;
  margin-bottom: 5rem;
}
.contenido p {
  margin-top: 5rem;
  font-size: 2.5rem;
  text-align: center;
  letter-spacing: 0.3rem;
}
.contenido p #bienvenido {
  filter: none;
  color: #CE9178;
}
.contenido p #echo {
  filter: none;
  color: #DCDCAA;
}
.contenido p span {
  color: #7ca22c;
  filter: drop-shadow(0px 0px 4px #adff00);
}

@media (min-width: 768px) {
  .contenido {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
  }
  .contenido img {
    max-width: none;
  }
  .contenido .texto-header {
    margin-right: 5rem;
  }
  .contenido .texto-header p {
    margin-top: 0;
    font-size: 3rem;
    line-height: 4rem;
    text-align: left;
  }
  .contenido .texto-header .texto-principal {
    font-size: 4rem;
  }
}
.centrado {
  width: 85%;
  margin: auto;
  z-index: 1;
  position: relative;
}
@media (min-width: 480px) {
  .centrado {
    width: 90%;
  }
}
@media (min-width: 768px) {
  .centrado {
    width: 95%;
  }
}

.proyectos p {
  text-align: center;
}

@media (min-width: 768px) {
  .proyectos {
    display: flex;
    overflow: scroll;
    overflow-y: hidden;
    flex-wrap: nowrap;
  }
  .proyectos .contenido-proyecto {
    padding-top: 5rem;
    height: 50rem;
    flex: 1 0 auto;
    width: 50rem;
    margin: 0 2rem;
  }
}
.contenedor-proyectos {
  margin: 0 auto;
  padding: 3rem 1rem;
  border-radius: 1rem;
  background-color: #252532;
}

main .encabezado-proyectos h1 {
  text-align: center;
}
main .encabezado-proyectos p {
  text-align: center;
  margin: 0 0 6rem 0;
}
@media (min-width: 768px) {
  main .encabezado-proyectos p {
    margin: 0;
  }
}
@media (min-width: 768px) {
  main {
    margin-top: 10rem;
  }
}

.contenido-proyecto {
  margin: 2rem auto;
  filter: grayscale(1);
  width: 100%;
  cursor: pointer;
}
.contenido-proyecto:hover {
  filter: grayscale(0);
  transition: 1s;
}

.box video {
  width: 100%;
}

.descripcion-proyecto img {
  height: 3rem;
}

.box .descripcion-proyecto {
  opacity: 0;
  height: 0;
  transition: all 400ms;
  visibility: hidden;
}
.box .descripcion-proyecto p {
  font-weight: 700;
  margin: 1rem 0;
}

.contenido-proyecto:hover .box {
  border: 1px solid #05060e;
  background-color: rgba(50, 50, 50, 0.9);
  padding-bottom: 1rem;
}
.contenido-proyecto:hover .box.box-color-7 {
  box-shadow: 0 0 50px -3px #4338CA;
}
.contenido-proyecto:hover .box.box-color-6 {
  box-shadow: 0 0 50px -6px #0da6f3;
}
.contenido-proyecto:hover .box.box-color-5 {
  box-shadow: 0 0 40px -5px #E08709, 0 0 24px 0px #71B100;
}
.contenido-proyecto:hover .box.box-color-4 {
  box-shadow: 0 0 40px -5px #F53756, 0 0 24px 0px #752F97;
}
.contenido-proyecto:hover .box.box-color-3 {
  box-shadow: 0 0 50px -3px #784D3C;
}
.contenido-proyecto:hover .box.box-color-2 {
  box-shadow: 0 0 50px -6px #9C27B0;
}
.contenido-proyecto:hover .box.box-color-1 {
  box-shadow: 0 0 38px -6px #0097A7, 0 0 30px 0 #FFC107;
}
.contenido-proyecto:hover .descripcion-proyecto {
  opacity: 1;
  visibility: visible;
  height: 7rem;
}

.iconos-video {
  display: flex;
  justify-content: space-evenly;
}

.contenedor-burbujas {
  position: relative;
  overflow: hidden;
  height: auto;
}

.burbujas {
  position: absolute;
  display: flex;
}

.burbujas span {
  position: relative;
  width: 3rem;
  height: 3rem;
  background-color: #CE9178;
  margin: 0 0.4rem;
  border-radius: 50%;
  box-shadow: 0 0 10px #a16e5a, 0 0 50px #CE9178, 0 0 100px #CE9178;
  animation: animate 23s linear infinite;
  animation-duration: calc(120s / var(--i));
}

.burbujas span:nth-child(even) {
  background-color: #2196f3;
  box-shadow: 0 0 10px #052cc8, 0 0 50px #2196f3, 0 0 100px #2196f3;
}

@keyframes animate {
  0% {
    transform: translateY(240vh) scale(0);
  }
  100% {
    transform: translateY(-10vh) scale(1);
  }
}
.element, #circle, .javascript, .sql, .redondo {
  visibility: hidden;
  display: none;
}

.element, #circle, .javascript, .sql, .redondo {
  visibility: visible;
  display: inherit;
}

.element {
  position: absolute;
  width: 4rem;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

#circle {
  position: relative;
  width: 15rem;
  height: 170px;
  margin: 10rem auto 0 auto;
}

.javascript {
  width: 3rem;
}

.sql {
  border-radius: 100%;
}

.redondo {
  position: relative;
  top: 20%;
  left: 2%;
  cursor: pointer;
  margin: 0 auto;
  width: 9rem;
}

@media (min-width: 375px) {
  .redondo {
    top: 7%;
    width: 14rem;
  }
}
@media (min-width: 768px) {
  .element {
    width: 8rem;
  }
  #circle {
    width: 40rem;
    height: 340px;
    margin: 18rem auto;
  }
  .javascript {
    width: 6rem;
  }
  .redondo {
    top: 3%;
    width: 30rem;
  }
}
.contenedor-tecnologias p {
  margin: 2rem;
}

.curriculum {
  border-top: 1px solid #252532;
  border-bottom: 1px solid #252532;
  transition-property: background-color;
  transition-duration: 0.3s;
}
.curriculum .botones {
  display: block;
}
.curriculum .botones .boton {
  background-color: #7ca22c;
  padding: 1.5rem 4rem;
  color: #05060e;
  margin-top: 2rem;
  font-size: 2rem;
  font-weight: 400;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
  margin: 5rem 0;
  width: 90%;
  margin: 3rem 2rem;
  padding: 1.5rem 2rem;
}
@media (min-width: 768px) {
  .curriculum .botones .boton {
    width: auto;
  }
}
.curriculum .botones .boton:hover {
  background-color: #5d7a21;
  cursor: pointer;
}
@media (min-width: 540px) {
  .curriculum .botones {
    display: flex;
    justify-content: space-evenly;
    align-items: center;
  }
  .curriculum .botones button {
    margin: 3rem 2rem;
  }
}
.curriculum:hover {
  background-color: #01061a;
}
.curriculum h2 {
  margin-top: 3rem;
}

.expandir.ocultar {
  width: 0;
  height: 0;
  visibility: hidden;
}
.expandir.mostrar {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 100;
  background-color: #252532;
  transition: all 700ms ease-out;
  width: 100%;
  height: 100vh;
  visibility: visible;
}

body.fijar-body {
  overflow: hidden;
}

#menu {
  overflow: auto;
}
#menu button {
  background-color: #7ca22c;
  padding: 1.5rem 4rem;
  color: #05060e;
  margin-top: 2rem;
  font-size: 2rem;
  font-weight: 400;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
  margin: 5rem 0;
  margin: 3rem;
  font-size: 3rem;
  font-weight: 900;
  padding: 2rem 3rem;
  border-radius: 50%;
  width: auto;
}
@media (min-width: 768px) {
  #menu button {
    width: auto;
  }
}
#menu button:hover {
  background-color: #5d7a21;
  cursor: pointer;
}
#menu .salir {
  display: flex;
  justify-content: flex-end;
}
#menu .salir #salir {
  margin-right: 2rem;
}
#menu .advertencia {
  position: absolute;
  top: 50%;
  left: 26%;
  right: 25%;
}
@media (min-width: 640px) {
  #menu .advertencia {
    display: none;
  }
}
#menu::-webkit-scrollbar {
  width: 15px;
  background-color: #252532;
}
#menu::-webkit-scrollbar-thumb {
  background-color: #2196f3;
  border-radius: 3rem;
}

.cv-contenido {
  display: none;
}
@media (min-width: 640px) {
  .cv-contenido {
    display: block;
    line-height: 3rem;
    font-family: "Montserrat", sans-serif;
    letter-spacing: 0.1rem;
    border: 1px solid #FFFFFF;
    margin-bottom: 5rem;
  }
}
@media (min-width: 768px) {
  .cv-contenido {
    display: grid;
    grid-template-columns: 37% 63%;
  }
}

.informacion-cv {
  background-color: #05060e;
  padding: 6rem 4rem;
}
.informacion-cv a {
  color: #FFFFFF;
  display: block;
}
.informacion-cv h1, .informacion-cv h3 {
  color: #DCDCAA;
  text-align: left;
}
.informacion-cv li {
  font-size: 2rem;
}
.informacion-cv h1 {
  font-size: 4.7rem;
  line-height: 5rem;
}
.informacion-cv h3 {
  padding-top: 5rem;
  text-transform: uppercase;
  font-size: 2.5rem;
}
@media (min-width: 768px) {
  .informacion-cv ul {
    padding-inline-start: 2rem;
  }
  .informacion-cv li {
    font-size: 2rem;
  }
  .informacion-cv h1 {
    font-size: 4.7rem;
    line-height: 5rem;
  }
  .informacion-cv h3 {
    padding-top: 5rem;
    text-transform: uppercase;
    font-size: 2.5rem;
  }
}

.border {
  border-bottom: 2px solid #FFFFFF;
  padding: 0 0 5rem;
}

.cuerpo-cv {
  padding: 3rem;
  background-color: #cacaca;
}
.cuerpo-cv h2, .cuerpo-cv h3, .cuerpo-cv h4, .cuerpo-cv p {
  color: #05060e;
