}
.cuerpo-cv p {
  font-weight: 500;
  margin-top: 0.5remp;
}
@media (min-width: 768px) {
  .cuerpo-cv {
    padding: 9rem 5rem;
  }
}

@media (min-width: 540px) {
  .datos {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr);
  }
  .datos .perfil {
    grid-column: 1/3;
    grid-row: 1/2;
  }
}
@media (min-width: 768px) {
  .datos {
    display: block;
  }
}

.datos-contacto {
  display: flex;
  align-items: center;
  word-break: break-word;
}
.datos-contacto svg {
  min-width: 30px;
}
.datos-contacto p {
  margin-right: 1rem;
}

.contenedor-formulario {
  background-image: url("https://www.thecolvinco.com/es/blog/wp-content/uploads/2018/11/shutterstock_174452435.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  padding: 3rem 1rem;
  margin-top: 10rem;
  margin-bottom: 5rem;
}

.formulario {
  margin: 3rem auto 0;
  max-width: 60rem;
  backdrop-filter: blur(13px);
  border: 1px solid #FFFFFF;
}
.formulario input, .formulario textarea {
  width: 100%;
}
.formulario textarea {
  max-width: 20rem;
  min-height: 20rem;
