  max-height: 30rem;
  flex: 1;
}
@media (min-width: 375px) {
  .formulario textarea {
    max-width: 56rem;
    min-width: 30rem;
  }
}
@media (min-width: 768px) {
  .formulario textarea {
    min-width: 27.6rem;
    max-width: 55.2rem;
    min-height: 20rem;
    max-height: 35rem;
  }
}
.formulario fieldset {
  padding: 2rem;
  border: none;
}
.formulario input[type=submit] {
  margin: 2rem 2rem 2rem auto;
}

.input-group {
  position: relative;
  margin-top: 2rem;
  width: 100%;
}

.input {
  border: 1px solid #FFFFFF;
  border-radius: 1rem;
  background: none;
  padding: 1rem;
  font-size: auto;
  color: #FFFFFF;
  transition: border 150ms cubic-bezier(0.4, 0, 0.2, 1);
}

.user-label {
  position: absolute;
  left: 15px;
  color: #ebebeb;
  pointer-events: none;
  transform: translateY(1rem);
  transition: 150ms cubic-bezier(0.4, 0, 0.2, 1);
}

.input:focus {
  outline: none;
}

.input:focus ~ label {
  transform: translateY(-90%) scale(0.8);
}

form .boton {
  background-color: #052cc8;
  padding: 1.5rem 4rem;
  color: #7ca22c;
  margin-top: 2rem;
  font-size: 2rem;
  font-weight: 400;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
  margin: 5rem 0;
  margin: 2rem 0 0 auto;
}
@media (min-width: 768px) {
  form .boton {
    width: auto;
  }
}
form .boton:hover {
  background-color: #042196;
  cursor: pointer;
}

input:-webkit-autofill,
input:-webkit-autofill:hover,
input:-webkit-autofill:focus {
  -webkit-box-shadow: 0 0 0px 1000px rgba(6, 8, 40, 0.9137254902) inset;
}

input:-webkit-autofill {
